.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px rgb(95, 93, 93);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background:white ;
    align-items: center;
    min-width: 29%;
}
.image{
    border-radius: 50%;
    width:100px;
    height: 100px;
}